"use strict";

function DummyTransformer() {
	function transform(data, interval) {
		var responseData = {};
		responseData[interval] = data;

		return responseData;
	};

	transform.options = function(opt) {
		return opt;
	};
	return transform;
}

export default DummyTransformer;



